/* for Mobile  */
@media (width<415px){
  .header img{
    height: 6rem;
  }
  .topbody{
    text-align: left;
    width: 80%;
    margin: auto;
  }
  .topbody .trophyimg{
    height: 15rem;
    margin: 2rem auto;
    margin-left: 4rem;
  }
  .topbody .personimg{
    height: 11rem;
  }


  .imgcollage{
    margin-top: 5rem;
  }
  .imgcollage .collage{
    width:80%;
    margin-top: 3rem;
  }
  .pgfooter hr{
    margin:1rem 2rem;
    border: 1px solid red;
  }
  .pgfooter div{
    margin: 1rem 3rem;
  }
  .pgfooter span{
    color:  red;
    font-weight:600;
  }
  .pgfooter .social span{
    color:  white;
  }
  .pgfooter .social img{
    width:30px;
    height: 30px;
  }
  .pgfooter .social{
    width: 96vw;
    height: fit-content;
    padding: 10px 0px;
    margin: 1vw 2vw; 
    background-color: red;
  }
  .pgfooter .social div{
    margin: 1rem auto;
  }
  .pgfooter .social div span{
    margin-bottom: 2rem;
  }
}
@media ((width>415px) and (width<720px)){

  .header img{
    height: 6rem;
  }
  .topbody{
    width:90%;
    margin:auto
  }
  .topbody .trophyimg{
    width: 20%;
    margin: 2rem auto;
  }
  .topbody .personimg{
    width: 100%;
  }
  .imgcollage{
    width: 90%;
    margin:auto;
    margin-top: 5rem;
  }
  .imgcollage .collage{
    width:100%;
    margin-top: 3rem;
  }
  .pgfooter{
    width:100%;
    margin: auto;
  }
  .pgfooter hr{
    width:90%;
    margin:1rem auto;
    border: 1px solid red;
  }
  .pgfooter div{
    margin: 1rem 3rem;
  }
  .pgfooter span{
    color:  red;
    font-weight:600;
  }
  .pgfooter .social span{
    color:  white;
  }
  .pgfooter .social img{
    width:30px;
    height: 30px;
  }
  .pgfooter .social{
    width: 96%;
    height: fit-content;
    padding: 10px 0px;
    margin: 1vw 2vw; 
    background-color: red;
  }
  .pgfooter .social div{
    margin: 1rem auto;
  }
  .pgfooter .social div span{
    margin-bottom: 2rem;
  }
}
@media (width>720px){

  .header img{
    height: 6rem;
  }
  .topbody{
    text-align: left;
    width: 90%;
    margin: auto;
    display: flex;
  }
  .topbody .trophyimg{
    width: 10rem;
    height: 20rem;
    margin: auto;
    /* height: ; */
    /* margin-top: 2rem; */

  }
  .topbody .personimg{
    width: 100%;
  }
  .imgcollage{
    width:90%;
    margin: auto;
    margin-top: 5rem;
  }
  .imgcollage .collage{
    width:100%;
    margin-top: 3rem;
  }
  .pgfooter{
    width: 100%;
  }
  .pgfooter hr{
    margin:1rem 2rem;
    border: 1px solid red;
  }
  .pgfooter div{
    margin: 1rem 3rem;
  }
  .pgfooter span{
    color:  red;
    font-weight:600;
  }
  .pgfooter .social span{
    color:  white;
  }
  .pgfooter .social img{
    width:30px;
    height: 30px;
  }
  .pgfooter .social{
    width: 96%;
    margin: auto;
    margin-bottom: 1%;
    background-color: red;
    display: flex;
  }
  .pgfooter .social div{
    margin: 1rem auto;
  }
  .pgfooter .social div span{
    margin-bottom: 2rem;
  }
}
@media (width>900px){
  .topbody .trophyimg{
    width: 20rem;
    height: 40rem;
